@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Poppins:wght@400;500&display=swap');

body, html, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #000;
}

.aspect-wrapper {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #2C5F99 0%, #0F2033 100%);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 48px 0 48px;
  position: relative;
  min-height: 180px;
  font-size: 5rem;
  gap: 32px;
}

.logo {
  margin-top: 0;
  margin-left: 0;
  width: 160px;
  height: 160px;
  object-fit: contain;
}

.org-info {
  flex: 1 0 0;
  min-width: 200px;
  margin-top: -150px;
  text-align: center;
  margin: 0 24px;
  font-size: 12rem;
  color: #222;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.home-logo {
  width: 220px;
  height: 220px;
}

.admin-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 40px;
  top: 110px;
  margin: 0;
  padding: 0;
}

.admin-icon {
  font-size: 4rem !important;
  display: block;
  margin: 0 auto;
  line-height: 1;
}

.admin-text {
  font-size: 2rem !important;
  margin: 0;
  margin-right: 15px;
  text-align: center;
  color: #0a0a0a;
  font-weight: 600;
  line-height: 1.5;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcome-title {
  color: #8B1C1C;
  font-size: 250rem;
  font-weight: bold;
  margin-bottom: 60px;
  margin-top: 0;
  text-align: center;
}

.main-image {
  width: 800px;
  height: 800px;
  object-fit: cover;
  border-radius: 8px;
  border: 4px solid #fff;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  margin-bottom: 48px;
}

.button-row {
  display: flex;
  gap: 18px;
  margin-bottom: 12px;
}

.primary-btn {
  background: #fff;
  color: #8B1C1C;
  font-weight: bold;
  font-size: 4rem;
  border: none;
  border-radius: 6px;
  padding: 32px 80px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: background 0.2s, color 0.2s;
}
.primary-btn:hover {
  background: #f9b233;
  color: #fff;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  padding: 40px 80px;
  margin-top: 120px;
  font-size: 2rem;
  color: #6d3a1c;
  background: none;
}

.footer-icon {
  font-size: 2rem;
  margin-right: 4px;
  color: #000;
}

.footer-link {
  color: #000;
  text-decoration: none;
  font-weight: 500;
  transition: text-decoration 0.2s;
}
.footer-link:hover {
  text-decoration: underline;
}

.footer-link, .footer-icon {
  font-size: 2.5rem;
}

.admin-btn, .admin-icon, .admin-text {
  font-size: inherit;
}

@media (max-width: 900px) {
  .aspect-wrapper {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
  .footer {
    flex-direction: column;
    gap: 8px;
    font-size: 0.95rem;
    padding: 10px 6px;
  }
}

@media (max-width: 1100px) {
  .main-bg {
    width: 100vw;
    min-width: 0;
    min-height: 0;
  }
  .main-content .sai-image {
    width: 50vw;
    height: auto;
  }
  .footer-content {
    flex-direction: column;
    gap: 12px;
  }
  .footer {
    padding: 18px;
  }
  .header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    padding: 18px 12px 0 12px;
  }
  .org-info {
    white-space: normal;
    overflow: visible;
    text-overflow: unset;
    text-align: left;
    font-size: 1.3rem;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .aspect-wrapper {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.home-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.home-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
  object-fit: contain;
}

.home-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}