.event-bg {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #222;
  }
  
  .event-aspect {
    width: 95vw;
    min-width: 0;
    max-width: 1200px;
    height: auto;
    min-height: 0;
    max-height: 95vh;
    aspect-ratio: 4 / 3;
    margin: 2vw auto;
    border-radius: 16px;
    box-shadow: 0 4px 32px rgba(0,0,0,0.15);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  }
  
  @media (max-width: 900px) {
    .event-aspect {
      width: 100vw;
      max-width: 100vw;
      min-width: 0;
      height: auto;
      max-height: 100vh;
      margin: 0;
      border-radius: 0;
      aspect-ratio: unset;
    }
    .event-footer {
      flex-direction: column;
      gap: 8px;
      font-size: 0.95rem;
      padding: 10px 6px;
    }
  }
  
  @media (min-width: 1024px) and (min-height: 1366px) {
    .event-aspect {
      width: 100vw;
      height: 100vh;
      max-width: none;
      max-height: none;
      aspect-ratio: unset;
      margin: 0;
      border-radius: 0;
    }
  }
  
  .event-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 48px 64px 0 64px;
    position: relative;
    font-size: 2rem;
  }
  
  .event-logo {
    margin-top: 50px;
    margin-left: -60px;
    width: 390px;
    height: 390px;
  }
  
  .event-header-centered {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: -100px;
    margin-left: -25px;
    background: none;
    border-bottom: none;
    
  }
  
  .event-org-info {
    flex: 1;
    text-align: center;
    font-size: 1.9rem;
    margin-left: -40px;
    margin-right: -50px;
    margin-top: -30px;
    color: #222;
    font-weight: 700;
  }
  
  .event-home-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 5rem;
    color: #8B1C1C;
    transition: color 0.2s;
    margin-right: 15px;
  }
  .event-home-btn:hover {
    color: #c86d3a;
    font-size: 5rem;
  }
  
  .event-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 0;
  }
  
  .event-title {
    color: #8B1C1C;
    font-size: 5rem;
    font-weight: bold;
    margin-bottom: 40px;
    margin-top: 60px;
    text-align: center;
  }
  
  .event-form-container {
    width: 100%;
    margin: 0 auto 16px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .event-form, .event-filter-row {
    width: 100%;
    max-width: 500px;
    margin: 0 auto 16px auto;
    display: flex;
    gap: 16px;
    justify-content: center;
  }
  
  .event-form input, .event-filter-row input {
    padding: 10px 12px;
    border-radius: 4px;
    border: none;
    background: #eaeaea;
    font-size: 15rem;
    width: 400px;
    min-width: 250px;
    max-width: 100%;  
  }
  
  
  .event-form button, .event-header-btn, .event-add-btn {
    padding: 10px 24px;
    border-radius: 6px;
    background: #fff;
    color: #8B1C1C;
    font-weight: bold;
    font-size: 1.5rem;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    transition: background 0.2s, color 0.2s;
  }
  .event-form button:hover, .event-header-btn:hover, .event-add-btn:hover {
    background: #f9b233;
    color: #fff;
  }
  
  .event-table-container {
    width: 100%;
    margin: 40px auto 24px auto;
    display: flex;
    justify-content: center;
    max-height: 60vh;
    overflow-y: scroll;
  }
  
  .event-table {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    background: #fff;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  }
  
  .event-table th {
    background: #8B1C1C;
    color: #fff;
    font-weight: bold;
    border-bottom: 2px solid #c86d3a;
    font-size: 3rem;
    padding: 20px 10px;
  }
  
  .event-table td {
    font-size: 3rem;
    padding: 18px 8px;
  }
  
  .event-table button, .event-table .event-header-btn, .event-table .event-add-btn {
    font-size: 2.2rem;
    padding: 16px 32px;
    border-radius: 8px;
    background: #fff;
    color: #8B1C1C;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    border: none;
    transition: background 0.2s, color 0.2s;
  }
  .event-table button:hover, .event-table .event-header-btn:hover, .event-table .event-add-btn:hover {
    background: #f9b233;
    color: #fff;
  }
  
  .event-table th, .event-table td {
    border: 1px solid #ccc;
  }
  
  .event-table th:first-child {
    border-top-left-radius: 12px;
  }
  .event-table th:last-child {
    border-top-right-radius: 12px;
  }
  
  .event-table img {
    width: 60px;
    height: auto;
    border-radius: 6px;
  }
  
  .event-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 18px;
    padding: 48px 80px;
    margin-top: 120px;
    font-size: 2.2rem;
    color: #6d3a1c;
    background: none;
  }
  
  .footer-icon {
    font-size: 5rem;
    margin-right: 4px;
    color: #000;
  }

  .event-header-row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 32px 0 32px;
    box-sizing: border-box;
  }

  .event-header-actions {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
    width: 90%;
    margin: 12px auto 0 auto;
  }

  .event-header-btn {
    background: #fff;
    color: #8B1C1C;
    border: none;
    border-radius: 6px;
    padding: 8px 18px;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    transition: background 0.2s, color 0.2s;
  }
  .event-header-btn:hover {
    background: #f9b233;
    color: #fff;
  }

  .event-add-btn {
    width: 100%;
    background: #8B1C1C;
    color: #fff;
    border: none;
    border-radius: 6px;
    padding: 8px 0;
    font-weight: bold;
    font-size: 1.5rem;
    cursor: pointer;
    margin-top: 0;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    transition: background 0.2s, color 0.2s;
  }
  .event-add-btn:hover {
    background: #f9b233;
    color: #ffffff;
  }

  .event-table input[type="text"],
  .event-table input[type="date"],
  .event-table input[type="time"],
  .event-table input[type="file"] {
    width: 100%;
    max-width: 260px;
    box-sizing: border-box;
    padding: 8px 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 3rem;
    height: 3.5rem;
  }

  .event-table input[type="file"] {
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    padding: 8px 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 2.5rem;
  }

  .event-table-container::-webkit-scrollbar {
    width: 12px;
    background: #eee;
  }
  .event-table-container::-webkit-scrollbar-thumb {
    background: #8B1C1C;
    border-radius: 8px;
    border: 2px solid #fff;
  }
  .event-table-container::-webkit-scrollbar-track {
    background: #eee;
  }

  .event-table-container::-webkit-scrollbar-button {
    background: #8B1C1C;
    height: 16px;
  }

  .event-table thead th {
    position: sticky;
    top: 0;
    z-index: 2;
    background: #8B1C1C;
    color: #fff;
  }

  .event-logout-btn {
    margin: 32px auto 0 auto;
    display: block;
    background: #fff;
    color: #b85c00;
    font-size: 3.5rem;
    font-weight: bold;
    border: 2px solid #ffb300;
    border-radius: 12px;
    padding: 12px 36px;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
    transition: background 0.2s, color 0.2s;
  }
  .event-logout-btn:hover {
    background: #ffe082;
    color: #ff6f00;
  }

  .event-registration-icon {
    background: none;
    border: none;
    border-radius: 0;
    color: #8B1C1C;
    font-size: 2rem;
    width: auto;
    height: auto;
    margin-right: 12px;
    cursor: pointer;
    transition: color 0.2s;
    position: relative;
    box-shadow: none;
  }
  .event-registration-icon:hover {
    color: #c86d3a;
    background: none;
    border: none;
  }

  .event-tabs {
    display: flex;
    align-items: center;
    gap: 4px;
    margin-right: 80px;
    font-size: 5rem;
    margin-top: 130px;
  }
  .event-tab-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 5rem;
    color: #8B1C1C;
    transition: color 0.2s;
    padding: 12px 18px;
  }
  .event-tab-btn svg {
    width: 5rem !important;
    height: 5rem !important;
  }
  .event-tab-btn:hover, .event-tab-btn:focus {
    background: #f9b233;
    font-size: 5rem;
    color: #fff;
  }
  .event-tab-label {
    margin-left: 6px;
    font-size: 1.1rem;
  }

  .event-footer-dark {
    background: none;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
  }
  .event-footer-dark, .event-footer-dark .footer-icon {
    color: #222;
  }

  .footer-link {
    color: #000;
    text-decoration: none;
    font-weight: 500;
    transition: text-decoration 0.2s;
    font-size: 2.5rem;
  }
  .footer-link:hover {
    text-decoration: underline;
  }

  .event-table th:nth-child(1), .event-table td:nth-child(1) { width: 28%; }
  .event-table th:nth-child(2), .event-table td:nth-child(2) { width: 12%; }
  .event-table th:nth-child(3), .event-table td:nth-child(3) { width: 10%; }
  .event-table th:nth-child(4), .event-table td:nth-child(4) { width: 18%; }
  .event-table th:nth-child(5), .event-table td:nth-child(5) { width: 18%; }
  .event-table th:nth-child(6), .event-table td:nth-child(6) { width: 14%; }

.event-table tr:first-child td:first-child input[type="text"],
.event-table tr:first-child td:first-child input:not([type]) {
    font-size: 3rem;
    width: 400px;
    min-width: 250px;
}

.event-table tr:first-child td:nth-child(4) input[type="text"],
.event-table tr:first-child td:nth-child(4) input:not([type]) {
    font-size: 3rem;
    width: 400px;
    min-width: 250px;
}