.admin-bg {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  align-items: stretch;
  justify-content: stretch;
}

.admin-login-main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.admin-login-card {
  background: transparent;
  width: 500px;
  padding: 40px 40px 30px 40px;
  border-radius: 10px;
  box-shadow: 0 0 0 8px rgba(0,0,0,0.08);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.admin-login-title {
  color: #fff;
  font-size: 150rem;
  margin-top: 90px;
  font-weight: bold;
  margin-bottom: 25;
  text-align: center;
}

.admin-lottie {
  width: 450px;
  height: 450px;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.admin-lottie > div, .admin-lottie svg, .admin-lottie canvas {
  width: 100% !important;
  height: 100% !important;
 
}

.admin-login-form {
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.admin-form-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 22px;
}

.admin-form-row label {
  width: 300px;
  text-align: right;
  margin-right: 50px;
  font-weight: bold;
  color: #fff;
  align-items: center;
  font-size: 3.5rem;
}

.admin-input {
  flex: unset;
  width: 400px;
  min-width: 400px;
  max-width: 100%;
  padding: 18px 18px;
  border-radius: 4px;
  border: none;
  background: #eaeaea;
  font-size: 3.5rem;
}

.admin-error {
  color: #000;
  margin-bottom: 10px;
  font-size: 2rem;
  text-align: center;
}

.admin-login-btn {
  margin: 200px auto 0 auto;
  padding: 18px 60px;
  border-radius: 6px;
  background: #fff;
  color: #8B1C1C;
  font-weight: bold;
  font-size: 4rem;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: background 0.2s, color 0.2s;
}
.admin-login-btn:hover {
  background: #f9b233;
  color: #fff;
}

.admin-home-section {
  position: absolute;
  top: 32px;
  right: 40px;
}

.admin-home-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  color: #000;
  transition: color 0.2s;
}

.admin-home-btn:hover {
  color: #8B1C1C;
}

.aspect-wrapper {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

@media (max-width: 900px) {
  .aspect-wrapper {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
  .footer {
    flex-direction: column;
    gap: 8px;
    font-size: 0.95rem;
    padding: 10px 6px;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .aspect-wrapper {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 48px 64px 0 64px;
  position: relative;
  font-size: 2rem;
}

.logo {
  width: 300px;
  height: 300px;
  margin-top: -50px;
  margin-left: -70px;
  
}

.org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -40px;
  margin-right: -50px;
  margin-top: -150px;
  color: #222;
  font-weight: 700;
}

.admin-btn, .admin-home-btn {
  background: none;
  border: none;
  color: #6d3a1c;
  font-size: 5rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.admin-icon, .admin-home-icon {
  font-size: 5rem;
  margin-top: 130px;
  margin-right: 20px;
}

.admin-text {
  font-size: 1rem;
  margin-top: -4px;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcome-title {
  color: #8B1C1C;
  font-size: 6rem;
  font-weight: bold;
  margin-bottom: 150px;
  margin-top: -500px;
  text-align: center;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  padding: 48px 80px;
  margin-top: 120px;
  font-size: 2.2rem;
  color: #6d3a1c;
  background: none;
}

.footer-icon {
  font-size: 2.5rem;
  margin-right: 4px;
  color: #000
}

.footer-link {
  color: #000;
  text-decoration: none;
  font-weight: 500;
  transition: text-decoration 0.2s;
  font-size: 2.5rem;
}
.footer-link:hover {
  text-decoration: underline;
}

body, html, #root {
  height: 100%;
  margin: 0;
  background: #222;
}
