.checkin-bg {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #222;
  }
  
  .checkin-aspect {
    width: 95vw;
    min-width: 0;
    max-width: 1200px;
    height: auto;
    min-height: 0;
    max-height: 95vh;
    aspect-ratio: 4 / 3;
    margin: 2vw auto;
    border-radius: 16px;
    box-shadow: 0 4px 32px rgba(0,0,0,0.15);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
  }
  
  @media (max-width: 900px) {
    .checkin-aspect {
      width: 100vw;
      max-width: 100vw;
      min-width: 0;
      height: auto;
      max-height: 100vh;
      margin: 0;
      border-radius: 0;
      aspect-ratio: unset;
    }
    .checkin-footer {
      flex-direction: column;
      gap: 8px;
      font-size: 0.95rem;
      padding: 10px 6px;
    }
  }
  
  @media (min-width: 1024px) and (min-height: 1366px) {
    .checkin-aspect {
      width: 100vw;
      height: 100vh;
      max-width: none;
      max-height: none;
      aspect-ratio: unset;
      margin: 0;
      border-radius: 0;
    }
  }
  
  .checkin-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 18px 24px 0 24px;
    margin-top: -15px;
    position: relative;
  }
  
  .checkin-logo {
    width: 160px;
    height: 160px;
    margin-top: -5px;
    margin-left: -30px;
    object-fit: contain;
  }
  
  .checkin-org-info {
    flex: 1;
    text-align: center;
    font-size: 1rem;
    margin-top: -90px;
    color: #222;
    font-weight: 500;
  }
  
  .checkin-home-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 2rem;
    color: #000;
    transition: color 0.4s;
    margin-right: -10px;
    margin-top: -40px;
  }
  
  .checkin-home-btn:hover {
    color: #8B1C1C;
  }
  
  .checkin-main {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 0;
  }
  
  .checkin-title {
    color: #fff;
    font-size: 4rem;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 25px;
    text-align: center;
    text-shadow: 0 2px 8px rgba(0,0,0,0.08);
  }
  
  .checkin-qr-scanner {
    width: 800px;
    height: 800px;
    min-width: 800px;
    min-height: 800px;
    margin: 40px auto 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0px;
    overflow: hidden;
  }
  
  .checkin-result {
    color: #fff;
    margin-top: 16px;
    font-weight: bold;
  }
  
  .checkin-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 0 auto;
    gap: 32px;
  }
  
  .checkin-result-col {
    display: flex;
    align-items: center;
    min-width: 220px;
    min-height: 220px;
    justify-content: center;
  }
  
  .checkin-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 18px;
    padding: 18px 24px;
    margin-top: -80px;
    font-size: 1rem;
    color: #6d3a1c;
    background: none;
  }
  
  .footer-icon {
    font-size: 5rem;
    margin-right: 4px;
    color: #000;
  }
  
  .checkin-welcome-msg {
    font-size: 4rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-top: -100px;
  }
  
  .checkin-event-banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 0 10px 0;
  }
  
  .checkin-banner-img {
    width:800px;
    height: 500px;
    
    border-radius: 18px;
    margin-bottom: 12px;
    box-shadow: 0 2px 12px rgba(0,0,0,0.10);
  }
  
  .footer-link {
    color: #000;
    text-decoration: none;
    font-weight: 500;
    transition: text-decoration 0.2s;
  }
  
  .footer-link:hover {
    text-decoration: underline;
  }

  .checkin-header-centered {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: -100px;
    margin-left: -25px;
    background: none;
    border-bottom: none;
  }
  
  .checkin-logo {
    margin-top: 100px;
    margin-left: 10px;
    width: 390px;
    height: 390px;
    object-fit: contain;
  }
  
  .checkin-org-info {
    flex: 1;
    text-align: center;
    font-size: 1.9rem;
    margin-left: -100px;
    margin-right: -60px;
    margin-top: -30px;
    color: #222;
    font-weight: 700;
  }
  
  .checkin-home-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 5rem;
    color: #8B1C1C;
    transition: color 0.2s;
    margin-right: 60px;
    margin-top: 200px;
    position: absolute;
    right: 10px;
    top: 20px;

  }
  
  .checkin-home-btn:hover {
    color: #c86d3a;
  }
  
  .checkin-main {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 80px;
    padding-top: 40px;
  }
  
  .checkin-title {
    color: #fff;
    font-size: 4.5rem;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 25px;
    text-align: center;
    text-shadow: 0 2px 8px rgba(0,0,0,0.08);
  }
  
  .checkin-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 80px;
    max-width: 1200px;
    margin: 0 auto;
    width: 100%;
    position: relative;
  }
  
  .checkin-qr-scanner {
    width: 520px;
    height: 520px;
    min-width: 320px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0;
  }
  
  .checkin-result-col {
    display: flex;
    align-items: center;
    min-width: 320px;
    min-height: 220px;
    justify-content: flex-start;
    font-size: 2.5rem;
  }

#reader {
  position: relative;
  width: 800px !important;
  height: 800px !important;
  background: #000;
  overflow: hidden;
}

#reader video {
  position: absolute !important;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  aspect-ratio: 1 / 1 !important;
  background: #000;
}