.register-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
}

.register-aspect {
  width: 95vw;
  min-width: 0;
  max-width: 1200px;
  height: auto;
  min-height: 0;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

@media (max-width: 900px) {
  .register-aspect {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
  .register-footer {
    flex-direction: column;
    gap: 8px;
    font-size: 0.95rem;
    padding: 10px 6px;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .register-aspect {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.register-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 32px 0 32px;
  position: relative;
}

.register-logo {
  width: 150px;
  height: 150px;
  margin-top: -20px;
  margin-left: -30px;
  object-fit: contain;
}

.register-org-info {
  flex: 1;
  text-align: center;
  font-size: 1rem;
  margin-top: -120px;
  color: #222;
  font-weight: 500;
}

.register-home-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  color: #000;
  transition: color 0.4s;
  margin-right: -20px;
  margin-top: -80px;
}

.register-home-btn:hover {
  color: #8B1C1C;
}

.register-main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-top: 80px;
  margin-bottom: 0;
  padding-top: 40px;
  padding-bottom: 0;
}

.register-title {
  color: #fff;
  font-size: 4rem;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 25px;
  text-align: center;
  text-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.register-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 80px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}

.register-lottie {
  width: 700px;
  height: 700px;
  min-width: 320px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 120px;
  margin-left: -100px;
}

.register-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: 0;
}

.register-form-row {
  margin-bottom: 50px;
}

/* New: Group phone and email fields in a single row */
.register-form-row.grouped {
  justify-content: space-between;
  gap: 18px;
}

.register-input-group {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.register-label {
  font-size: 3.5rem;
  font-weight: bold;
  color: #fff;
  margin-right: 40px;
  min-width: 220px;
  text-align: right;
}

.register-input {
  font-size: 3.5rem;
  padding: 14px 16px;
  min-width: 350px;
}

.register-btn {
  border-radius: 6px;
  border: none;
  background: #fff;
  color: #8B1C1C;
  font-weight: bold;
  font-size: 3.5rem;
  cursor: pointer;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: background 0.2s, color 0.2s;
  padding: 14px 48px;
  margin-top: 60px;
  margin-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.register-btn:hover {
  background: #f9b233;
  color: #fff;
}

.register-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  padding: 18px 24px;
  margin-top: 70px;
  font-size: 1rem;
  color: #6d3a1c;
  background: none;
}

.footer-icon {
  font-size: 8rem;
  margin-right: 4px;
  color: #000;
}

.register-event-banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 24px 0 24px 0;
}
.register-banner-img {
  width: 800px;
  height: 500px;
 
  border-radius: 18px;
  margin-bottom: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.10);
}
.register-welcome-msg {
  font-size: 4rem;
  font-weight: bold;
  color: #8B1C1C;
  text-align: center;
  margin-top: -180px;
}

.footer-link {
  color: #000;
  text-decoration: none;
  font-weight: 500;
  transition: text-decoration 0.2s;
}
.footer-link:hover {
  text-decoration: underline;
}

.register-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.register-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
  object-fit: contain;
}

.register-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}

.register-home-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 5rem;
  color: #8B1C1C;
  transition: color 0.2s;
  margin-right: 60px;
  margin-top: 200px;
  position: absolute;
  right: -10px;
  top: -10px;
}

.register-home-btn:hover {
  color: #c86d3a;
} 