/* 12:9 aspect ratio container for iPad Pro, Figma style, matching EventDetailsPage */
.adminreg-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
}

.adminreg-aspect {
  width: 95vw;
  max-width: 1200px;
  max-height: 95vh;
  aspect-ratio: 4 / 3;
  margin: 2vw auto;
  border-radius: 16px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.15);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f9b233 0%, #c86d3a 100%);
}

.adminreg-header-centered {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: -100px;
  margin-left: -25px;
  background: none;
  border-bottom: none;
}

.adminreg-back-btn {
  background: none;
  border: none;
  color: #1976d2;
  font-size: 4rem;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s;
  position: absolute;

  top: 200px;
  right: 60px;
}
.adminreg-back-btn:hover {
  color: #0d47a1;
}

.adminreg-logo {
  margin-top: 100px;
  margin-left: 10px;
  width: 390px;
  height: 390px;
}

.adminreg-org-info {
  flex: 1;
  text-align: center;
  font-size: 1.9rem;
  margin-left: -100px;
  margin-right: -60px;
  margin-top: -30px;
  color: #222;
  font-weight: 700;
}

.adminreg-title {
  color: #8B1C1C;
  font-size: 5rem;
  font-weight: bold;
  margin-bottom: 40px;
  margin-top: 60px;
  text-align: center;
}

.adminreg-table-container {
  flex: 1 1 auto;
  min-height: 200px;
  max-height: 60vh;
  overflow-y: scroll;
  margin-bottom: 24px;
  display: flex;
  scrollbar-color: #8B1C1C #eee;
  scrollbar-width: thin;
}

.adminreg-table {
  margin: 0 auto;
  width: 100%;
  max-width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.adminreg-table th {
  background: #8B1C1C;
  color: #fff;
  font-weight: bold;
  border-bottom: 2px solid #c86d3a;
  font-size: 3rem;
  padding: 20px 10px;
}

.adminreg-table td {
  font-size: 3rem;
  padding: 18px 8px;
}

.adminreg-filter-input {
  width: 90%;
  margin-top: 6px;
  padding: 6px 10px;
  border: 1.5px solid #b3c6e6;
  border-radius: 8px;
  font-size: 3rem;
  background: #f7fbff;
  color: #222;
  transition: border 0.2s;
}
.adminreg-filter-input:focus {
  border: 2px solid #8B1C1C;
  outline: none;
}

.adminreg-logout-btn {
  margin: 32px auto 0 auto;
  display: block;
  background: #fff;
  color: #b85c00;
  font-size: 3.5rem;
  font-weight: bold;
  border: 2px solid #ffb300;
  border-radius: 12px;
  padding: 12px 36px;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  transition: background 0.2s, color 0.2s;
}
.adminreg-logout-btn:hover {
  background: #ffe082;
  color: #ff6f00;
}

.adminreg-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 18px;
  padding: 48px 80px;
  margin-top: 0;
  font-size: 2.2rem;
  color: #6d3a1c;
  background: none;
}

.footer-icon {
  font-size: 2.5rem;
  margin-right: 4px;
  color: #000;
}

.footer-link {
  color: #000;
  text-decoration: none;
  font-weight: 500;
  transition: text-decoration 0.2s;
  font-size: 2.5rem;
}
.footer-link:hover {
  text-decoration: underline;
}

.adminreg-table-container::-webkit-scrollbar {
  width: 12px;
  background: #eee;
}
.adminreg-table-container::-webkit-scrollbar-thumb {
  background: #8B1C1C;
  border-radius: 8px;
  border: 2px solid #fff;
}
.adminreg-table-container::-webkit-scrollbar-track {
  background: #eee;
}

.adminreg-table thead th {
  position: sticky;
  top: 0;
  z-index: 2;
  background: #8B1C1C;
  color: #fff;
}

/* Responsive styles */
@media (max-width: 900px) {
  .adminreg-aspect {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    height: auto;
    max-height: 100vh;
    margin: 0;
    border-radius: 0;
    aspect-ratio: unset;
  }
  .adminreg-footer {
    flex-direction: column;
    gap: 8px;
    font-size: 0.95rem;
    padding: 10px 6px;
  }
  .adminreg-table-container {
    width: 98vw;
    padding: 8px 2px;
  }
  .adminreg-title {
    font-size: 2rem;
    margin: 18px 0 12px 0;
  }
  .adminreg-logo {
    width: 80px;
    height: 80px;
    margin-left: 8px;
    margin-right: 8px;
  }
}

@media (min-width: 1024px) and (min-height: 1366px) {
  .adminreg-aspect {
    width: 100vw;
    height: 100vh;
    max-width: none;
    max-height: none;
    aspect-ratio: unset;
    margin: 0;
    border-radius: 0;
  }
}

.adminreg-main-content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 0;
}

.adminreg-table th, .adminreg-table td {
  border: 1px solid #ccc;
  text-align: center;
}

/* Prevent Event Date from wrapping and adjust column widths */
.adminreg-table th:nth-child(1), .adminreg-table td:nth-child(1) { width: 14%; }
.adminreg-table th:nth-child(2), .adminreg-table td:nth-child(2) { width: 22%; }
.adminreg-table th:nth-child(3), .adminreg-table td:nth-child(3) { width: 14%; }
.adminreg-table th:nth-child(4), .adminreg-table td:nth-child(4) { width: 18%; }
.adminreg-table th:nth-child(5), .adminreg-table td:nth-child(5) {
  width: 13%;
  min-width: 130px;
  white-space: nowrap;
}
.adminreg-table th:nth-child(6), .adminreg-table td:nth-child(6) { width: 8%; } 